<template>
    <v-card>
        <v-toolbar density="compact" color="primary">
            <v-btn icon @click="$emit('close')">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>設定</v-toolbar-title>
            <v-spacer></v-spacer>
            <!-- <v-toolbar-items> -->
            <!-- <v-btn @click="$emit('close')">
                <v-icon start icon="mdi-content-save-outline"></v-icon
                >保存</v-btn
            > -->
            <!-- </v-toolbar-items> -->
        </v-toolbar>
        <v-list lines="two" subheader>
            <v-list-subheader>表示設定</v-list-subheader>
            <v-list-item
                title="TODO一覧の表示設定"
                subtitle="TODO一覧の表示設定を変更します"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-switch
                            v-model="dispSettings.dispComplete"
                            color="primary"
                            label="完了を表示"
                            @change="onChangeDispSettings"
                            hide-details
                        ></v-switch>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <v-switch
                            v-model="dispSettings.dispUnnecessary"
                            color="primary"
                            label="やらないTODOを表示"
                            @change="onChangeDispSettings"
                            hide-details
                        ></v-switch>
                    </v-col>
                </v-row>
            </v-container>
            <v-divider></v-divider>
            <v-list-subheader>データの操作</v-list-subheader>
            <v-list-item
                title="データの出力"
                subtitle="データベースのデータをJSON形式で出力します"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-btn color="warning" @click="outputDB"
                            >データ出力</v-btn
                        >
                    </v-col>
                </v-row>
            </v-container>
            <v-list-item
                title="データの入力"
                subtitle="JSON形式のファイルをデータベースに入力します"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-file-input
                            v-model="inputFile"
                            label="入力データJson"
                            accept="application/json"
                            show-size
                        ></v-file-input>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <v-btn color="warning" @click="inputDB"
                            >データ入力</v-btn
                        >
                    </v-col>
                </v-row>
            </v-container>
            <v-list-item
                title="データパッチ"
                subtitle="データベースにパッチをあてます"
            ></v-list-item>
            <v-container>
                <v-row>
                    <v-col cols="12">
                        <v-btn color="warning" @click="patchDB"
                            >データパッチ</v-btn
                        >
                    </v-col>
                </v-row>
            </v-container>
        </v-list>
    </v-card>
</template>

<script>
import saveAs from "file-saver";

export default {
    name: "AppSettings",
    components: {},
    data: () => ({
        inputFile: null,
        dispSettings: {},
    }),
    mounted() {
        this.dispSettings = this.getDispSettings();
    },
    methods: {
        outputDB() {
            const outputJson = this.getTodoData();
            const output = JSON.stringify(outputJson);

            const blob = new Blob([output], {
                type: "application/json",
            });

            saveAs(blob, "todo.json");
        },
        async inputDB() {
            const data = await this.readInputFile();

            var inputJson = JSON.parse(data);
            this.setTodoData(inputJson);
        },
        readInputFile() {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = () => {
                    resolve(reader.result);
                };
                reader.onerror = reject;
                reader.readAsText(this.inputFile[0]);
            });
        },
        patchDB() {
            const todo = this.getTodoData();
            todo.list.forEach((t) => {
                t.unnecessary = false;
            });
            this.setTodoData(todo);
        },
        onChangeDispSettings() {
            this.updateDispSettings(this.dispSettings);
            this.$store.dispatch("increment");
        },
    },
};
</script>
