<template>
    <v-data-table
        :headers="headers"
        :items="todoList"
        :search="search"
        :custom-filter="filterItem"
        class="elevation-1"
        density="compact"
        item-key="id"
    >
        <template #[`item.unnecessary`]="{ item }">
            <v-checkbox
                v-model="item.raw.unnecessary"
                @change="onChangeCheck"
                color="primary"
                hide-details
            ></v-checkbox>
        </template>
        <template #[`item.progress_per`]="{ item }">
            <v-progress-linear
                v-model="item.columns.progress_per"
                color="success"
                height="25"
            >
                <template v-slot:default="{ value }">
                    <strong>{{ Math.ceil(value) }}%</strong>
                </template>
            </v-progress-linear>
        </template>
        <template #[`item.edit`]="{ item }">
            <v-dialog v-model="new_todo_dialog[item.index]" width="80%">
                <template v-slot:activator="{ props }">
                    <v-btn variant="text" v-bind="props">
                        <v-icon icon="mdi-pencil-outline"></v-icon>
                    </v-btn>
                </template>
                <NewTodo
                    :mode="1"
                    :todo="item.raw"
                    @close="new_todo_dialog[item.index] = false"
                />
            </v-dialog>
            <v-btn variant="text">
                <v-icon icon="mdi-trash-can-outline"></v-icon>
                <v-overlay
                    activator="parent"
                    location="start center"
                    location-strategy="connected"
                    scroll-strategy="block"
                >
                    <v-btn @click="onDelete(item.raw)" color="warning"
                        >削除する</v-btn
                    >
                </v-overlay>
            </v-btn>
        </template>
    </v-data-table>
</template>

<script>
import NewTodo from "./NewTodo";

export default {
    name: "TodoList",
    components: { NewTodo },
    computed: {
        count() {
            return this.$store.getters.getCount;
        },
    },
    watch: {
        count() {
            console.log("refresh!!");
            this.todoList = this.getTodoList();
            this.initDialogFlg();
            this.dispSettings = this.getDispSettings();
            this.search = new Date().toLocaleString();
        },
    },
    data: () => ({
        todoList: [],
        headers: [
            {
                title: "やらない",
                key: "unnecessary",
                // filterable: true,
                // filter: (value, search, item) => {
                //     console.log(value);
                //     console.log(search);
                //     console.log(item);
                // },
            },
            {
                title: "カテゴリ",
                key: "category",
            },
            {
                title: "タイトル",
                key: "title",
            },
            {
                title: "進捗率",
                key: "progress_per",
            },
            {
                title: "編集",
                key: "edit",
            },
        ],
        new_todo_dialog: [],
        search: "",
        dispSettings: {},
    }),
    beforeMount() {
        this.todoList = this.getTodoList();
        this.initDialogFlg();
        this.dispSettings = this.getDispSettings();
        this.search = new Date().toLocaleString();
    },
    methods: {
        initDialogFlg() {
            this.new_todo_dialog = [...Array(this.todoList.length)].map(
                () => false
            );
        },
        onDelete(todo) {
            this.deleteTodo(todo);
            this.$store.dispatch("increment");
        },
        onChangeCheck() {
            this.setTodoList(this.todoList);
        },
        filterItem(value, search, item) {
            if (!this.dispSettings.dispUnnecessary && item.unnecessary) {
                return false;
            }

            if (!this.dispSettings.dispComplete && item.progress_per >= 100) {
                return false;
            }

            return true;
        },
    },
};
</script>
