<script>
export default {
    install(Vue) {
        Vue.mixin({
            methods: {
                getTodoData() {
                    return this.getData("todo");
                },
                setTodoData(data) {
                    this.setData("todo", data);
                },
                getDispSettings() {
                    const data = this.getTodoData();
                    if (!data.dispSettings) {
                        return {
                            dispComplete: true,
                            dispUnnecessary: true,
                        };
                    }
                    return data.dispSettings;
                },
                updateDispSettings(dispSettings) {
                    const data = this.getTodoData();
                    data.dispSettings = dispSettings;
                    this.setTodoData(data);
                },
                addTodo(todo) {
                    const data = this.getTodoData();
                    if (!data.list) {
                        data.list = [];
                    }
                    data.list.push(todo);
                    this.setTodoData(data);
                },
                getTodoList() {
                    const data = this.getTodoData();
                    if (!data.list) {
                        return [];
                    }
                    return data.list;
                },
                setTodoList(list) {
                    const data = this.getTodoData();
                    data.list = list;
                    this.setTodoData(data);
                },
                deleteTodo(_todo) {
                    const data = this.getTodoData();
                    if (!data.list) {
                        return;
                    }

                    const filterData = data.list.filter((todo) => {
                        return todo.id != _todo.id;
                    });
                    data.list = filterData;
                    this.setTodoData(data);
                },
                updateTodo(_todo) {
                    const data = this.getTodoData();
                    if (!data.list) {
                        return;
                    }

                    const filterData = data.list.filter((todo) => {
                        return todo.id == _todo.id;
                    });
                    if (filterData.length == 0) {
                        return;
                    }
                    filterData[0].title = _todo.title;
                    filterData[0].category = _todo.category;
                    filterData[0].update_date = _todo.update_date;
                    filterData[0].total = _todo.total;
                    filterData[0].progress = _todo.progress;
                    filterData[0].progress_per = _todo.progress_per;

                    this.setTodoData(data);
                },
                getData(key) {
                    var data = JSON.parse(localStorage.getItem(key));
                    if (data == null) {
                        return {};
                    }
                    return data;
                },
                setData(key, data) {
                    localStorage.setItem(key, JSON.stringify(data));
                },
            },
        });
    },
};
</script>
